@include font('OpenSans Bold', opensans-bold);
@include font('OpenSans Bold Italic', opensans-bolditalic);
@include font('OpenSans Italic', opensans-italic);
@include font('OpenSans Regular', opensans-regular);
@include font('Roboto Bold', roboto-bold);
@include font('Roboto Bold Italic', roboto-bolditalic);
@include font('Roboto Italic', roboto-italic);
@include font('Roboto Light', roboto-light);
@include font('Roboto Light Italic', roboto-lightitalic);
@include font('Roboto Medium', roboto-medium);
@include font('Roboto Medium Italic', roboto-mediumitalic);
@include font('Roboto Regular', roboto-regular);


@mixin light {
	font-weight: 300; }

@mixin reg {
	font-weight: 400; }

@mixin med {
	font-weight: 500; }

@mixin bold {
	font-weight: 700; }

@mixin roboto {
  font-family: Roboto, Arial, sans-serif; }

@mixin rlight {
  // +roboto
  // +light
  font-family: 'Roboto Light', Arial, sans-serif; }

@mixin rreg {
  // +roboto
  // +reg
  font-family: 'Roboto Regular', Arial, sans-serif; }

@mixin rmed {
  // +roboto
  // +med
  font-family: 'Roboto Medium', Arial, sans-serif; }

@mixin rbold {
  font-family: 'Roboto Bold', Arial, sans-serif; }

@mixin oreg {
	// font-family: "Open Sans", Roboto, Arial, sans-serif
	font-family: 'OpenSans Regular', 'Roboto', Arial, sans-serif; }
@mixin obold {
	// font-family: "Open Sans", Roboto, Arial, sans-serif
	font-family: 'OpenSans Bold', 'Roboto Bold', Arial, sans-serif; }
